.header {
  h1 {
    text-align: center;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-image: linear-gradient(
    -225deg,
    #492dae 0%,
    #44107a 29%,
    #63720e 67%,
    #da9b49 100%
  );
  background-size: auto auto;
  background-clip: border-box;
  background-size: 200% auto;
  color: #fff;
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: textclip 2s linear infinite;
  // display: inline-block;
  font-size: 4vw;
  }
  @keyframes textclip {
    to {
      background-position: 200% center;
    }
  }
  // h1 {
  //   font-size: 3.3rem;
  //   text-shadow: 2px 3px 0px $color-1;
  //   text-align: center;
  //   letter-spacing: 2px;
  //   color:$color-4;
  // }
  nav {
    position: absolute;
    top: 16px;
    left: 16px;
    font-size: 2rem;

    ul {
      display: flex;
      flex-direction: column;
      width: 180px;
      justify-content: space-between;

      li {
        margin-top: 15px;
      }
      a {
        text-decoration: none;
        color: $black;
        border-radius: 10px;
        width: 102px;
        text-align: center;
        margin-right: 5px;
        padding: 4px 8px;

        &:visited {
          text-decoration: none;
          color: $black;
        }
        &:hover {
          background: $color-3;
        }
      }
      a.nav-active {
        background: $color-2;

        &:hover {
          background: $white;
        }
      }
    }
  }
}
